'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  channel: function channel(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: stream_type
    if (!data.channelID) return callback('channelID is required');
    var params = {};
    if (data.stream_type) params.stream_type = data.stream_type;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/streams/".concat(data.channelID, "?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  live: function live(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: channel, game, language, stream_type, limit, offset
    var params = {};
    if (data.channel) params.channel = data.channel;
    if (data.game) params.game = data.game;
    if (data.language) params.language = data.language;
    if (data.stream_type) params.stream_type = data.stream_type;
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/streams/?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  summary: function summary(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: game
    var params = {};
    if (data.game) params.game = data.game;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/streams/summary?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  featured: function featured(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: limit, offset
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/streams/featured?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  followed: function followed(data, callback) {
    // Authentication: user_read
    // Required Parameters: none
    // Optional Parameters: stream_type, limit, offset
    if (!data.auth) return callback('auth is required');
    var params = {};
    if (data.stream_type) params.stream_type = data.stream_type;
    if (data.limit) params.limit = data.limit;
    if (data.offset) params.offset = data.offset;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/streams/followed?".concat(querystring.stringify(params));
    options.auth = data.auth;
    request('GET', options, callback);
  }
};