'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  getClip: function getClip(data, callback) {
    // Authentication: none
    // Required Parameters: slug
    // Optional Parameters: none
    if (!data.slug) return callback('slug is required');
    var options = {};
    options.v4 = true;
    options.url = "https://api.twitch.tv/kraken/clips/".concat(data.slug);
    request('GET', options, callback);
  },
  top: function top(data, callback) {
    // Authentication: none
    // Required Parameters: none
    // Optional Parameters: channel, cursor, game, language, limit, period, trending
    var params = {};
    if (data.channel) params.channel = data.channel;
    if (data.cursor) params.cursor = data.cursor;
    if (data.game) params.game = data.game;
    if (data.language) params.language = data.language;
    if (data.limit) params.limit = data.limit;
    if (data.period) params.period = data.period;
    if (data.trending) params.trending = data.trending;
    var options = {};
    options.v4 = true;
    options.url = "https://api.twitch.tv/kraken/clips/top?".concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  followed: function followed(data, callback) {
    // Authentication: user_read
    // Required Parameters: none
    // Optional Parameters: limit, cursor, trending
    if (!data.auth) return callback('auth is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.cursor) params.cursor = data.cursor;
    if (data.trending) params.trending = data.trending;
    var options = {};
    options.v4 = true;
    options.url = "https://api.twitch.tv/kraken/clips/followed";
    request('GET', options, callback);
  }
};