'use strict';

var request = require('./request');

var querystring = require('querystring');

module.exports = {
  getMetadata: function getMetadata(data, callback) {
    // Authentication: none
    // Required Parameters: collectionID
    // Optional Parameters: none
    if (!data.collectionID) return callback('collectionID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID);
    request('GET', options, callback);
  },
  getCollection: function getCollection(data, callback) {
    // Authentication: none
    // Required Parameters: collectionID
    // Optional Parameters: include_all_items
    if (!data.collectionID) return callback('collectionID is required');
    var params = {};
    if (data.include_all_items) params.include_all_items = data.include_all_items;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID, "/items?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  getByChannel: function getByChannel(data, callback) {
    // Authentication: none
    // Required Parameters: channelID
    // Optional Parameters: limit, cursor, containing_item
    if (!data.channelID) return callback('channelID is required');
    var params = {};
    if (data.limit) params.limit = data.limit;
    if (data.cursor) params.cursor = data.cursor;
    if (data.containing_item) params.containing_item = data.containing_item;
    var options = {};
    options.url = "https://api.twitch.tv/kraken/channels/".concat(data.channelID, "/collections?").concat(querystring.stringify(params));
    request('GET', options, callback);
  },
  create: function create(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: channelID, title
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.channelID) return callback('channelID is required');
    if (!data.title) return callback('title is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/channels/".concat(data.channelID, "/collections");
    options.form = {
      tile: data.title
    };
    request('POST', options, callback);
  },
  update: function update(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: collectionID, title
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.collectionID) return callback('collectionID is required');
    if (!data.title) return callback('title is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID);
    options.form = {
      tile: data.title
    };
    request('PUT', options, callback);
  },
  createThumbnail: function createThumbnail(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: collectionID, itemID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.collectionID) return callback('collectionID is required');
    if (!data.itemID) return callback('itemID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID, "/thumbnail");
    options.form = {
      item_id: data.itemID
    };
    request('PUT', options, callback);
  },
  delete: function _delete(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: collectionID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.collectionID) return callback('collectionID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID);
    request('DELETE', options, callback);
  },
  addItem: function addItem(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: collectionID, videoID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.collectionID) return callback('collectionID is required');
    if (!data.videoID) return callback('videoID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID, "/items");
    options.form = {
      id: data.videoID,
      type: 'video'
    };
    request('POST', options, callback);
  },
  delItem: function delItem(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: collectionID, itemID
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.collectionID) return callback('collectionID is required');
    if (!data.itemID) return callback('itemID is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID, "/items/").concat(data.itemID);
    request('DELETE', options, callback);
  },
  moveItem: function moveItem(data, callback) {
    // Authentication: collections_edit
    // Required Parameters: collectionID, itemID, position
    // Optional Parameters: none
    if (!data.auth) return callback('auth is required');
    if (!data.collectionID) return callback('collectionID is required');
    if (!data.itemID) return callback('itemID is required');
    if (!data.position) return callback('position is required');
    var options = {};
    options.url = "https://api.twitch.tv/kraken/collections/".concat(data.collectionID, "/items/").concat(data.itemID);
    options.form = {
      position: data.position
    };
    request('PUT', options, callback);
  }
};